import { makeStyles } from '@material-ui/core'
import React from 'react'
import Chatdemo from '../components/HomePage/ChatDemo'
import loop_Logo from '../assets/Loop 4.png'

const useStyles = makeStyles((theme) => ({
    previewMain: {
        background: theme.palette.common.black,
        paddingTop:'60px',
        paddingBottom: '200px'
    },
    previewHeadText:{
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '64px',
        color: '#FFFFFF',
        margin:'0 auto',
        width:'80%',
    },
    previewText:{
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        width:'80%',
        margin:'20px auto'
    },
    previewLoop:{
        marginLeft:'-100px'
    }
}))
function Preview() {
    const classes = useStyles()
    return (
        <div className={classes.previewMain}>
            <h5 className={classes.previewHeadText}>Preview <img src={loop_Logo} className={classes.previewLoop} width='120' height='60' alt='loop'/></h5>
            <p className={classes.previewText}>
                This chatbot was trained on a document explaining Chatbase, You can embed a widget like this on any page on your website!
            </p>
            <Chatdemo />
        </div>
    )
}

export default Preview