import React from 'react';
import { Route, HashRouter, useLocation, Switch } from 'react-router-dom';
import Header from './components/Header';
import Footer from './components/Footer';
import HeaderCopy from './components/HeaderCopy';
import Home from './pages/Home';
import Login from './pages/Login';
import SignUp from './pages/SignUp';
import theme from './Utils/Theme';
import { ThemeProvider } from '@material-ui/core/styles';
import Preview from './pages/Preview';
import Invite from './pages/Invite';
import Integrates from './pages/Integrates';

function FooterWithConditionalRendering() {
  const location = useLocation();
  const isLoginOrSignup = location.pathname === '/login' || location.pathname === '/signup';

  return !isLoginOrSignup ? <Footer /> : null;
}

function App() {
  return (
    <>
    <ThemeProvider theme={theme}>
      <HashRouter>
        {/* <Header /> */}
        <HeaderCopy/>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/signup" component={SignUp} />
            <Route exact path="/preview" component={Preview} />
            <Route exact path="/invite" component={Invite} />
            <Route exact path="/integrates" component={Integrates} />
          </Switch>
          <FooterWithConditionalRendering/>
      </HashRouter>
      </ThemeProvider>
    </>
  );
}

export default App;