import { makeStyles } from '@material-ui/core'
import React from 'react'
import Chatdemo from '../components/HomePage/ChatDemo'
import loop_Logo from '../assets/Loop 4.png'
import friends_line  from '../assets/friends_line.png'

const useStyles = makeStyles((theme) => ({
    inviteMain: {
        background: theme.palette.common.black,
        paddingTop:'60px',
        paddingBottom: '200px'
    },
    inviteHeadText:{
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '64px',
        color: '#FFFFFF',
        margin:'0 auto',
        width:'80%',
    },
    inviteText:{
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        width:'80%',
        margin:'20px auto'
    },
    inviteLoop:{
        marginLeft:'-100px'
    }
}))
function Invite() {
    const classes = useStyles()
    return (
        <div className={classes.inviteMain}>
            <h5 className={classes.inviteHeadText}>Invite A Friends<img src={friends_line} className={classes.inviteLoop} width='180px' alt='loop'/></h5>
            <p className={classes.inviteText}>
            Join Friends of Chatbase.co and receive a 20% commission on all payments within the first 12 months for paying customers you refer to
            </p>
            <Chatdemo />
        </div>
    )
}

export default Invite